body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  height: 100vh;
}

#clock-container {
  width: 80%;
  margin: auto;
  height: 600px;
  border: 2px solid grey;
  border-radius: 5px;
  display: flex;
}
#controls-container {
  display: flex;
  flex-direction: column;
  width: 450px;
  justify-content: center;
}
#controls-container > * {
  height: 33%;
  justify-content: center;
  align-items: center;
  background-color: #92b170;
}
#controls {
  display: flex;
  flex-direction: row;
  border: 1px solid grey;
}
#title {
  display: flex;
  font-size: 40px;
  height: 100%;
  font-weight: bold;
  align-items: center;
  text-align: center;
  background-color: #9cb085;
}
.control {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50%;
  align-items: center;
}
#session-label,
#break-label {
  font-weight: bold;
}
.inc-dec {
  display: flex;
  flex-direction: row;
  width: 100%;
  font-size: 24px;
  margin-top: 10px;
  justify-content: space-around;
}
#buttons-container {
  display: flex;
  flex-direction: row;
}
#start_stop {
  width: 75%;
  height: 100%;
  text-align: center;
  background-color: #80b04c;
  display: flex;
  align-items: center;
  justify-content: center;
}
#reset {
  background-color: #75ae9b;
  width: 25%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
#clock {
  display: flex;
  flex-direction: column;
  background-color: #a9b1a0;
  width: 100%;
}
#timer-label {
  text-align: center;
  font-size: 40px;
  font-weight: bold;
  width: 100%;
  margin-top: 75px;
}
#time-left {
  text-align: center;
  margin-top: 125px;
  font-size: 100px;
  width: 100%;
}
